.loader {
    animation:spin 1s infinite linear;
    border:solid 2vmin transparent;
    border-radius:50%;
    border-right-color:#09f;
    border-top-color:#09f;
    box-sizing:border-box;
    height:10vmin;
    left:calc(50% - 5vmin);
    position:fixed;
    top:calc(50% - 5vmin);
    width:10vmin;
    z-index:1;
  }
  @keyframes spin {
    100% {
      transform:rotate(360deg);
    }
  }
a:focus {
  outline: 0
}

#wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}

#wrapper #content-wrapper {
  background-color: #fafdfb;
  width: 100%;
  overflow-x: hidden
}

#wrapper #content-wrapper #content {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto
}

.container,
.container-fluid,
.container-login {
  padding-left: 1.5rem;
  padding-right: 1.5rem
